import React, { Component, Fragment } from 'react'
import { Link } from 'react-router-dom'
import Board from './Board'
import {
  makeDeck,
  cardToggle,
  reshuffle,
  removeSelected as removeSelectedCards,
  isSet,
  nameThird,
  handleGoogleRedirect,
  countSets,
  getAdjustmentFactor,
} from '../utils/helpers'
import { shuffle, cloneDeep, isEmpty } from 'lodash'
import { colors } from '../config'
import update from 'immutability-helper'
import Slider from 'react-rangeslider'
import axios from 'axios'
import { connect } from 'react-redux'
import Signout from './Signout'
import InputRange from 'react-input-range'

import { Players, GameState, SoloState } from '../utils/models'

const debugging = true

const config = {
  turnTime: 4000,
  colors,
  playingTo: 6,
  cpuDelay: 1600,
}

const calculateIntervalFromDifficulty = (d: number, board?: string[]) => {
  let diff = Number(d)
  if (Number.isNaN(diff)) {
    diff = 1
  }
  let interval = 30000 / (5 * diff)
  if (board !== undefined) {
    const newSets = countSets(board)
    interval = interval * getAdjustmentFactor(newSets)
  }
  return interval
}

const createGameState = () => {
  const initialDeck = makeDeck()
  const selected: string[] = []
  return {
    ...reshuffle({
      deck: initialDeck.slice(12),
      board: initialDeck.slice(0, 12),
    }),
    selected,
  }
}

const logTime = (msg = '') => {
  const d = new Date()
  const s = (d.getTime() % 10 ** 6) / 1000
  console.log(msg, s.toFixed(1))
}

const initialState = {
  players: {
    you: {
      score: 0,
      color: config.colors[0],
    },
    cpu: {
      score: 0,
      color: config.colors[1],
    },
  },
  gameStarted: false,
  myName: 'you',
  setFound: false,
  declarer: null,
  gameOver: null,
  cpuTurnInterval: 1000,
  startTime: new Date(),
  cpuGuessCounter: 0,
  paused: false,
}

class Solo extends Component<any, SoloState> {
  constructor(props: any) {
    super(props)
    this.state = {
      ...cloneDeep(initialState),
      ...createGameState(),
      difficulty: 2,
    }
  }

  handleStartGame = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault()
    this.setState({
      gameStarted: true,
      startTime: new Date(),
    })

    this.activateCpu(config.cpuDelay)
  }

  componentDidMount = () => {
    const savedDifficulty = window.localStorage.getItem('soloDifficulty')
    let difficulty = savedDifficulty ? Number(savedDifficulty) : 2
    const cpuTurnInterval = calculateIntervalFromDifficulty(difficulty)
    this.setState({
      difficulty,
      cpuTurnInterval,
    })
  }

  componentWillUnmount = () => {
    if (this.state.cpuTimer !== null) {
      window.clearInterval(this.state.cpuTimer)
    }
  }

  activateCpu = (delay: number) => {
    const { board, difficulty } = this.state

    if (this.state.initCpuTimerId) {
      window.clearTimeout(this.state.initCpuTimerId)
    }

    // @ts-ignore
    const initialTurnInterval = calculateIntervalFromDifficulty(difficulty, board)
    console.log(`Turns every ${initialTurnInterval} ms`)

    const initCpuTimerId = window.setTimeout(() => {
      const cpuTimer = window.setInterval(this.cpuTurn, initialTurnInterval)
      this.setState({
        cpuTimer,
      })
    }, delay)
    this.setState({
      initCpuTimerId: initCpuTimerId,
    })
  }

  stopCpu = () => {
    this.state.initCpuTimerId && window.clearTimeout(this.state.initCpuTimerId)
    this.state.cpuTimer && window.clearInterval(this.state.cpuTimer)
  }

  cpuTurn = () => {
    const { board, declarer, gameOver, paused } = this.state
    if (declarer || gameOver || paused) {
      return
    }
    this.setState((prevState) => {
      return {
        cpuGuessCounter: prevState.cpuGuessCounter + 1,
      }
    })

    if (debugging) {
      logTime('Guess')
    }
    const [a, b] = shuffle(board).slice(0, 2)
    const c = nameThird(a, b)
    if (board.includes(c)) {
      this.setState({
        declarer: 'cpu',
        selected: [a],
        cpuFound: [b, c],
        setFound: true,
      })
      if (this.state.cpuTimer !== null) {
        window.clearInterval(this.state.cpuTimer)
      }
      this.setState({
        cpuAnimation: window.setInterval(this.animateCpuChoice, 900),
      })
    }
  }

  handlePause = () => {
    if (this.state.declarer) {
      return
    }
    if (!this.state.paused) {
      this.stopCpu()
    } else {
      this.activateCpu(500)
    }
    this.setState((prevState) => {
      return {
        paused: !prevState.paused,
      }
    })
  }

  animateCpuChoice = () => {
    const { selected, cpuFound } = this.state
    const cpuCopy = [...cpuFound]
    if (cpuCopy.length === 0) {
      return
    }
    const newSelected = [...selected, cpuCopy.pop() as string]
    this.setState({
      cpuFound: cpuCopy,
      selected: newSelected,
    })
    if (newSelected.length === 3) {
      if (this.state.cpuAnimation !== null) {
        window.clearInterval(this.state.cpuAnimation)
      }
      this.updateSelected(newSelected, 'cpu')
    }
  }

  updatePlayerScore = (myName: string, delta: number): [Players, number] => {
    const { players } = this.state
    const newScore = players[myName].score + delta
    const newPlayers = update(players, {
      [myName]: {
        $merge: {
          score: newScore,
        },
      },
    })
    return [newPlayers, newScore]
  }

  expireDeclare = () => {
    const { declarer, selected } = this.state
    if (declarer && !isSet(selected)) {
      const [newPlayers] = this.updatePlayerScore(declarer, -0.5)
      this.setState({
        players: newPlayers,
        declarer: null,
        timeDeclared: undefined,
        selected: [],
      })
    }
  }

  markPointForDeclarer = (declarer: string) => {
    const [newPlayers, newScore] = this.updatePlayerScore(declarer, 1)
    const { user } = this.props.userReducer
    const gameOver = newScore >= config.playingTo ? declarer : ''
    const newState = {
      players: newPlayers,
      gameOver,
    }
    if (!isEmpty(gameOver)) {
      const uid = (user && user.uid) || 'anonymous'
      const player_won = declarer == 'you' ? 1 : 0
      const total_time = Math.round((new Date().getTime() - this.state.startTime.getTime()) / 1000)
      axios
        .post('/api/game', {
          uid,
          total_time,
          player_won,
          difficulty_level: this.state.difficulty,
          winning_score: newScore,
        })
        .then(() => {
          console.log('Game sent')
        })
        .catch((err) => {
          console.log('Error sending game')
        })
    }
    this.setState(newState)
    return newState
  }

  performDeclare = (declarer: string) => {
    if (!this.state.declarer) {
      const timeNow = new Date().getTime()
      const update = {
        declarer,
        timeDeclared: timeNow,
      }
      this.setState(update)
      this.setState({
        undeclareId: window.setTimeout(() => {
          this.expireDeclare()
        }, config.turnTime),
      })
    }
  }

  updateSelected = (newSelected: Array<string>, declarer: string) => {
    const newState = {
      setFound: isSet(newSelected),
      selected: newSelected,
      declarer,
    }
    if (newState.setFound) {
      this.state.undeclareId && clearTimeout(this.state.undeclareId)
      setTimeout(() => {
        this.removeSet()
      }, 2000)
    }
    this.setState(newState)
  }

  handleCardClick = (card: string) => {
    const { setFound, declarer, myName, paused } = this.state
    if (paused) {
      return
    }
    if (!setFound && declarer !== 'cpu') {
      const newSelected = cardToggle(card, this.state.selected)
      if (!declarer) {
        this.performDeclare(myName)
      }
      this.setState({
        selected: newSelected,
      })
      if (isSet(newSelected)) {
        this.updateSelected(newSelected, 'you')
      }
    }
  }

  handleRedeal = () => {
    const { declarer, difficulty, paused } = this.state
    this.state.initCpuTimerId && window.clearTimeout(this.state.initCpuTimerId)
    if (!declarer && !paused) {
      const { deck, board } = reshuffle(this.state)
      this.state.cpuTimer && window.clearInterval(this.state.cpuTimer)

      this.activateCpu(config.cpuDelay * 0.5)
      this.setState({ deck, board })
    }
  }

  removeSet = () => {
    const { declarer, selected } = this.state
    if (declarer && isSet(selected)) {
      console.log('Set found, removing')
      this.markPointForDeclarer(declarer)
      const { board, deck, selected } = removeSelectedCards(this.state)
      const newState = {
        setFound: false,
        declarer: null,
        timeDeclared: undefined,
        board,
        deck,
        selected,
      }
      this.setState(newState)

      this.state.cpuTimer && window.clearInterval(this.state.cpuTimer)

      this.activateCpu(config.cpuDelay)
    }
  }

  unsetTimers = (cpuTimer: number, timeoutId: number) => {
    window.clearInterval(cpuTimer)
    window.clearTimeout(timeoutId)
  }

  resetGame = () => {
    this.state.cpuTimer && window.clearInterval(this.state.cpuTimer)
    this.setState({
      ...cloneDeep(initialState),
      ...createGameState(),
    })
  }

  render() {
    const { board, deck, selected, declarer, players, gameStarted, setFound } = this.state
    const { userReducer } = this.props
    const { user } = userReducer
    if (userReducer.loading) {
      return 'Loading...'
    }
    if (!gameStarted) {
      // GameSettings
      return (
        <div className="container main-content">
          {user !== null && <Signout />}
          <h3 className="text-center mb-4">Solo Play vs. Computer</h3>
          <h4 className="mb-4">Choose difficulty level:</h4>
          <div className="row">
            <div className="col-12">
              <form onSubmit={this.handleStartGame}>
                <div className="col-10 col-md-6 mb-5">
                  <InputRange
                    maxValue={6}
                    minValue={1}
                    //@ts-ignore
                    value={this.state.difficulty}
                    //@ts-ignore
                    onChange={(difficulty) => {
                      //@ts-ignore
                      const cpuTurnInterval = calculateIntervalFromDifficulty(difficulty, board)
                      window.localStorage.setItem('soloDifficulty', difficulty.toString())
                      //@ts-ignore
                      this.setState({
                        cpuTurnInterval,
                        // @ts-ignore
                        difficulty,
                      })
                    }}
                  />
                </div>
                <input type="submit" value="Start" className="btn btn-primary" />
              </form>
              <p style={{ marginTop: '24px' }}>First to {config.playingTo} points is the winner</p>
            </div>
            <div className="row mt-4">
              <ul style={{ listStyleType: 'none' }}>
                <li className="mb-4">
                  <Link to="/local">Local Multiplayer</Link>
                </li>
                <li>
                  <Link to="/">Back to Main Menu</Link>
                </li>
              </ul>
            </div>
          </div>
          {!user && (
            <div className="row mt-4">
              <div>
                <p>To save your game statistics, sign in with your Google account.</p>

                <p>
                  <button onClick={handleGoogleRedirect} className="btn btn-info">
                    Sign in
                  </button>
                </p>
              </div>
            </div>
          )}
        </div>
      )
    }
    return (
      <React.Fragment>
        <Board
          board={board}
          deck={deck}
          selected={selected}
          declarer={declarer}
          handleCardClick={this.handleCardClick}
          handleDeclare={() => {}}
          handleRedeal={this.handleRedeal}
          players={players}
          setFound={this.state.setFound}
          gameOver={this.state.gameOver}
          myName={this.state.myName}
          resetGame={this.resetGame}
          solo={true}
          gameMode="versus"
          cpuGuessCounter={this.state.cpuGuessCounter}
          handlePause={this.handlePause}
          paused={this.state.paused}
        />
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state: any) => ({
  userReducer: state.user,
})

export default connect(mapStateToProps)(Solo)
